<template>
  <div id="app">
    <div class="toubu">
        <div>当前组件:{{goton}}</div>
    </div>
    <div class="router-view">
       <div>流程总集合:{{liuchenglist}}</div>
       <div>当前流程:[{{dangqianliucheng}}]</div>
       <div>当前流程序号:[{{dangqianliuchengxuhao}}]</div>
      <router-view></router-view>
    </div>
    <div class="dibu nextbut" v-show='butshow' @click='gonext'>  <span>{{butname}}</span> </div> 
  </div>
</template>

<script>
import vue from 'vue'
import resource from 'vue-resource'
vue.use(resource)
export default {
  name: 'app',
  data () {
    return {
      goton: 'root',
      n: 0,
      butname: '下一步',
      liuchenglist: [],
      dangqianliucheng: '',
      liuchengnamelist: [],
      dangqianurl: '',
      butshow: true,
      dangqianliuchengxuhao: ''
    }
  },
  created: function () {
    this.dangqianurl = this.$route.path
    this.n = this.dangqianurl.slice(-1)
    this.dangqianurl = this.dangqianurl.slice(1)
    this.goton = this.dangqianurl
    var url = 'https://www.easy-mock.com/mock/59a92b9fe0dc66334198ddf9/example/urlqwerty'
    this.$http.post(url, {emulateJSON: true})
    .then(function (res) {
      var liuchengjihe = res.body.data
      for (var i in liuchengjihe) {
        this.liuchengnamelist.push(liuchengjihe[i].name)
      }
      this.liuchenglist = this.liuchengnamelist
      this.$http.post('https://www.easy-mock.com/mock/59a92b9fe0dc66334198ddf9/example/liucheng', {emulateJSON: true})
      .then(function (data) {
        var benciliuchengjihe = data.body.data.name
        this.dangqianliucheng = benciliuchengjihe
        for (var n = 0; n < this.liuchengnamelist.length; n++) {
          console.log(this.liuchengnamelist[2])
          if (this.liuchengnamelist[n] === this.dangqianliucheng) {
            console.log(n)
            this.dangqianliuchengxuhao = n
            this.$router.push(this.liuchengnamelist[this.dangqianliuchengxuhao])
            break
          } else {
            console.log('无')
          }
        }
      }, function (data) {
        console.log(data)
      })
    }, function (res) {
      console.log(res)
    })
    .catch(function (res) {
      console.log(res)
    })
  },
  methods: {
    gonext: function () {
      for (var n = 0; n < this.liuchengnamelist.length; n++) {
        if (this.liuchengnamelist[n] === this.dangqianliucheng) {
          this.dangqianliuchengxuhao++
          if (this.dangqianliuchengxuhao > this.liuchengnamelist.length - 1) {
            this.dangqianliuchengxuhao = this.liuchengnamelist.length - 1
          } else {
            this.$router.push(this.liuchengnamelist[this.dangqianliuchengxuhao])
          }
          break
        } else {
          console.log('无')
        }
      }
    }
  }
}
</script>

<style>
*{
  margin:0px;
  padding:0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.toubu{
  height: 45px;
  width:100%; 
  background-color:blueviolet;
  line-height: 45px;
  color:#fff;
}
 a{
  text-decoration:none;
}
.toubu span{
  color:#fff
}

#root{
  color:chartreuse;
}
.router-view {
  height: 200px;
  width: 100%;
  background-color: aqua;
}
.router-link-exact-active span, .router-link-active span{
  color:#fff;
}

p{
  color:brown;
  font-size:30px;
  line-height: 200px;
}

.nextbut{
  height: 30px;
  width:100px;
  background-color:cornflowerblue;
  -webkit-box-shadow:1px 1px 5px 2px rgba(0,0,0,0.3);
  margin:0 auto;
  border-radius:3px;
  margin-top:20px;
  color:#fff;
  line-height: 30px;
  cursor: pointer;
}

.nextbut:active{
   -webkit-box-shadow:1px 1px 1px 1px rgba(0,0,0,0.1);
}
</style>



